body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #47257a;
  text-align: center;
  color: white;
}

.counter {
  font-weight: bold;
  font-size: 20px;
  font-color: red;
}

.header {
  text-align: center;
}
.header .banner,
.instructions img {
  max-width: 80%;
  text-align: center;
}

.collection {
  min-width: 100px;
}

.active {
  border: 2px dotted red;
  padding: 5px;
  display: inline-block;
}

.instructions {
  text-align: left;
  width: 80%;
  max-width: 760px;
  margin: 0 auto;
}



